.orders-container {
  width: 100%;
  height: 100%;

  .alert-enter {
    opacity: 0;
    transform: scale(0.9);
  }

  .alert-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 300ms, transform 300ms;
  }

  .alert-exit {
    opacity: 1;
  }

  .alert-exit-active {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 300ms, transform 300ms;
  }

  .modal-sloy {
    padding-top: 20px;
    padding-bottom: 20px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: auto;


    .modal-card {
      width: max-content;
      height: max-content;
      background-color: white;
      box-shadow: 0 33px 97px 0 #002C5714;
      border-radius: 12px;

      .add-driver {
        margin: 20px;
        width: max-content;
        height: max-content;
        position: relative;

        .cancel-btn {
          width: 100%;
          display: flex;
          justify-content: flex-end;

          img {
            cursor: pointer;
            width: 25px;
            transition: 0.3s;
          }

          &:hover {
            img {
              transform: scale(1.2);
            }
          }
        }

        .title {
          text-align: center;
          font-size: 22px;
          line-height: 32px;
        }

        .title-form {
          margin-top: 10px;
          font-size: 18px;
          color: #00c389;
        }

        .form-container {
          margin-top: 5px;

          .select-box {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .select-sides-file {
              width: 250px;
              height: 40px;
              margin-right: 10px;
              position: relative;
              border-radius: 4px;
              border: 1px solid #ccc;
              margin-left: 8px;

              input {
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                position: absolute;
                opacity: 0;
              }

              overflow: hidden;

              .sloy-image {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-left: 20px;
                padding-right: 20px;
                cursor: pointer;

                img {
                  width: 25px;
                }
              }

              .active {
                background-color: rgba(0, 195, 137, 0.2);
              }
            }

            .select-sides {
              width: 250px;
              margin-right: 10px;

              .selectMui {
                .MuiOutlinedInput-root {
                  &:hover {
                    .MuiOutlinedInput-notchedOutline {
                      border: 1px solid rgba(0, 173, 173, 1);
                    }
                  }
                }

                .MuiOutlinedInput-root.Mui-focused {
                  .MuiOutlinedInput-notchedOutline {
                    border: 1px solid rgba(0, 173, 173, 1);
                  }
                }

                .MuiInputLabel-root.Mui-focused {
                  color: rgba(0, 173, 173, 1);
                }
              }

              .on-of {
                margin-top: 5px;
                display: flex;
                align-items: center;
                width: 90%;
                height: 40px;
                border-radius: 8px;
                padding: 3px;
                background-color: rgba(180, 192, 204, 0.16);

                .on {
                  background-color: white;
                  color: rgba(38, 45, 51, 1);
                }

                .of {
                  cursor: pointer;
                  width: 50%;
                  height: 100%;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  border-radius: 8px;
                  color: rgba(133, 145, 158, 1);
                  transition: 0.3s;
                }
              }

              .title-on {
                color: rgb(135, 135, 135);
                font-size: 14px;
                font-weight: 400;
              }

              .textField {
                .MuiOutlinedInput-root {
                  &:hover {
                    .MuiOutlinedInput-notchedOutline {
                      border: 1px solid #00c389;
                    }
                  }
                }

                .MuiOutlinedInput-root.Mui-focused {
                  .MuiOutlinedInput-notchedOutline {
                    border: 1px solid #00c389;
                  }
                }

                .MuiInputLabel-root.Mui-focused {
                  color: #00c389;
                }
              }
            }

            .select-sides-time {
              margin-top: 5px;
              width: 220px;
              margin-right: 10px;

              label {
                color: rgb(135, 135, 135);
                font-size: 14px;
                font-weight: 400;
              }

              input {
                padding: 15px;
                margin-top: 5px;
                width: 100%;
                height: 40px;
                border-radius: 10px;
                border: none;
                background-color: rgba(180, 192, 204, 0.16);

                &:focus {
                  outline: 1px solid rgba(0, 173, 173, 1);
                }

                font-size: 14px;
              }
            }
          }

        }

        .add-btn {
          position: absolute;
          right: -90px;
          bottom: -16px;
          width: 60px;
          height: 60px;
          border-radius: 50%;
          background-color: #00c389;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;

          &:hover {
            img {
              transform: scale(1.1);
            }
          }

          img {
            width: 40%;
            transition: 0.2s;
          }
        }
      }

      .driver-photo {
        margin: 10px;
        width: 600px;
        height: max-content;

        .cancel-btn {
          width: 100%;
          display: flex;
          justify-content: flex-end;

          img {
            cursor: pointer;
            width: 30px;
            transition: 0.3s;
          }

          &:hover {
            img {
              transform: scale(1.2);
            }
          }
        }

        .photo {
          margin-top: 10px;
          width: 100%;
          height: 100%;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 8px;
          }
        }
      }

      .car-information {
        margin: 30px;
        width: max-content;
        height: max-content;

        .cancel-btn {
