          line-height: 32px;
        }

        .information {
          margin-top: 20px;
          margin-right: 100px;

          .info {
            display: flex;
            align-items: center;

            .title {
              font-size: 18px;
              color: #717070;
              margin-right: 10px;
            }

            .text {
              font-size: 20px;
              color: #5a5959;

            }
          }
        }
      }

      .blocked {
        margin: 30px;
        width: 300px;
        height: max-content;

        .title {
          text-align: center;
          font-size: 24px;
          line-height: 32px;
        }

        .reason-text {

          textarea {
            margin-top: 10px;
            width: 100%;
            height: 100px;
            margin-top: 10px;
            border: 1px solid #717070;
            border-radius: 8px;
            padding: 8px;

          }

          .buttons {
            margin-top: 10px;
            width: 100%;
            justify-content: space-between;
            display: flex;
            align-items: center;

            .cancel {
              padding: 8px 30px;
              border-radius: 8px;
              cursor: pointer;
              background-color: #eeecec;
              transition: 0.3s;

              &:hover {
                background-color: #dfdddd;
              }

            }

            .success {
              padding: 8px 30px;
              border-radius: 8px;
              cursor: pointer;
              background-color: #f27e7e;
              transition: 0.3s;

              &:hover {
                background-color: #f16a6a;
              }
            }
          }
        }
      }

